<template>
    <div class="login">
        <el-row>
            <el-switch v-model="isRegister" class="mb-2"
                active-text="注册" inactive-text="登陆" />
        </el-row>
        <el-row>
            <el-input v-model="username" autofocus prefix-icon="User"
                size="large" maxlength="30" placeholder="用户名" />
        </el-row>
        <el-row>
            <el-input v-model="password" show-password prefix-icon="Key"
                size="large" maxlength="30" placeholder="密码" />
        </el-row>
        <el-row v-if="isRegister">
            <el-input v-model="passwordConfirm" show-password prefix-icon="Key"
                size="large" maxlength="30" placeholder="确认密码" />
        </el-row>
        <el-row>
            <el-button v-if="isRegister" @click="register" type="primary"
                :disabled="!Boolean(username && password && passwordConfirm)">注册</el-button>
            <el-button v-else @click="login" type="primary"
                :disabled="!Boolean(username && password)">登陆</el-button>
        </el-row>
    </div>
</template>

<script>
import AV from 'leancloud-storage';

export default {
    name: 'Login',
    data () {
        return {
            username: '',
            password: '',
            passwordConfirm: '',
            isRegister: false,
        }
    },
    methods: {
        login () {
            if (!this.username || !this.password) return
            AV.User.logIn(this.username, this.password).then(() => {
                this.$message({ message: '登陆成功。', type: 'success' })
                this.$router.push('/')
            }).catch(e => this.$store.commit('catchError', e))
        },
        register () {
            if (!this.username || !this.password || !this.passwordConfirm) return
            if (this.password == this.passwordConfirm) {
                const user = new AV.User()
                user.setUsername(this.username)
                user.setPassword(this.password)
                user.signUp().then(() => {
                    this.$message({ message: '注册成功。', type: 'success' })
                    this.$router.push('/')
                }).catch(e => this.$store.commit('catchError', e))
            } else {
                this.$message({ message: '两次输入的密码不一致。', type: 'warning' })
            }
        },
    }
}
</script>

